@use '../base/variables' as v;
@use '../base/mixins' as m;

.testimonial{
    background-color: v.$verde;
    font-size: 2.4rem;
    padding: 2rem;
    color: v.$blanco;
    border-radius: 2rem;

    blockquote{
        padding-left: 5rem;
        position: relative;

        &::before{
            content: '';
            background-image: url(../img/comilla.svg);
            background-repeat: no-repeat;
            width: 5rem;
            height: 8rem;
            display: block;
            position: absolute;
            left: 0;
            top: -1rem;
        }

    }
    p{
        color: v.$blanco;
        text-align: right;
    }
}