@use '../base/mixins' as m;
@use '../base/variables' as v;

.derecha{
    display: flex;
    flex-direction: column;
    align-items: center;

    @include m.tablet{
        align-items: flex-end;
    }

    .dark-mode-boton{
        width: 3rem;
        margin: 3rem 0;
        filter: invert(100%);

        &:hover{
            cursor: pointer;
        }

        @include m.tablet{
            margin-top: 0;
        }
    }
}

.barra{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 3rem 0;
    
    @include m.tablet{
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
    }

}

.navegacion{
    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .3s ease-in-out, opacity .5s linear, height .5s linear;

    @include m.tablet{
        opacity: 1;
        visibility: visible;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }

    a{
        color: v.$blanco;
        text-decoration: none;
        font-size: 1.8rem;
        display: block;
        text-align: center;
        margin-right: 2rem;

        &:last-of-type{
            margin-right: 0;
        }

        &:hover{
            color: v.$verde;
        }
    }
}

/*Menu hamburgesa*/

.mobile-menu{
    @include m.tablet{
        display: none;
    }

    img{
        width: 5rem;
    }
}

.mostrar{
    visibility: visible;
    opacity: 1;
    height: auto;
}