@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  margin: 0;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 100rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

/** Media Queries **/
.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-amarillo:hover {
  background-color: rgb(199.4849785408, 120.2253218884, 8.0150214592);
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: rgb(199.4849785408, 120.2253218884, 8.0150214592);
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: rgb(96.7203389831, 151.5, 0);
  cursor: pointer;
}

.boton-rojo-block {
  background-color: #a30202;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  width: 100%;
}
.boton-rojo-block:hover {
  background-color: rgb(137.8090909091, 1.6909090909, 1.6909090909);
  cursor: pointer;
}

.boton-verde-block {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-verde-block:hover {
  background-color: rgb(96.7203389831, 151.5, 0);
  cursor: pointer;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.dark-mode {
  background-color: rgb(25.5, 25.5, 25.5);
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .anuncio, .dark-mode .resumen-propiedad {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .anuncio .precio, .dark-mode .resumen-propiedad .precio {
  color: #71B100;
}
.dark-mode .anuncio .icono, .dark-mode .resumen-propiedad .icono {
  filter: invert(100%);
}
.dark-mode .formulario label, .dark-mode .formulario legend, .dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario .input input, .dark-mode .formulario .input textarea, .dark-mode .formulario .input select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}

/* UI */
.no.webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: bold;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  background-color: #333333;
  padding: 2.5rem 0;
  margin-bottom: 0;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .contenido-footer {
  padding-top: 2.5rem;
}
.footer .copyright {
  text-align: center;
  color: #FFFFFF;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin: 3rem 0;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    opacity: 1;
    visibility: visible;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 1.8rem;
  display: block;
  text-align: center;
  margin-right: 2rem;
}
.navegacion a:last-of-type {
  margin-right: 0;
}
.navegacion a:hover {
  color: #71B100;
}

/*Menu hamburgesa*/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.iconos-nosotros .icono {
  text-align: center;
}
.iconos-nosotros .icono img {
  height: 10rem;
}
.iconos-nosotros .icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: rgb(237.75, 237.75, 237.75);
  border: 1px solid #e1e1e1;
  margin-bottom: 2rem;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio .contenido-anuncio h3, .anuncio .contenido-anuncio p {
  margin: 0;
}
.anuncio .contenido-anuncio h3 {
  font-size: 2.5rem;
}

.precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
  margin-bottom: 3rem;
  max-width: 40rem;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

.no-webp .imagen-contacto {
  background-color: url(../img/encuentra.jpg);
}

.webp .imagen-contacto {
  background-image: url(../img/encuentra.webp);
}

.imagen-contacto {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2, .imagen-contacto p {
  color: #FFFFFF;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
}
.imagen-contacto p {
  font-size: 1.8rem;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog.last-of-type {
  margin-bottom: 0;
}

.informacion-meta span {
  color: #E08709;
}

.texto-entrada a {
  color: #000000;
}
.texto-entrada h4 {
  margin: 0;
  line-height: 2rem;
  margin-top: 1.5rem;
}
@media (min-width: 768px) {
  .texto-entrada h4 {
    margin-top: 0;
  }
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1.5rem;
}
.texto-entrada p {
  line-height: 2rem;
}
.texto-entrada span {
  color: #E08709;
  line-height: 2rem;
}

.testimonial {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 5rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: 0;
  top: -1rem;
}
.testimonial p {
  color: #FFFFFF;
  text-align: right;
}

@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros h3 {
  font-size: 2.2rem;
  font-weight: 900;
  text-align: left;
  margin: 3rem 0 0 0;
  padding-left: 1rem 0 3rem 0;
}

.formulario fieldset {
  border: 3px solid #e1e1e1;
}
.formulario fieldset p {
  font-size: 1.4rem;
  color: #333333;
}
.formulario fieldset label {
  font-weight: 900;
  text-transform: uppercase;
  font-size: 1.3rem;
  margin-right: 2rem;
}
.formulario fieldset .input {
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
}
.formulario fieldset .input input, .formulario fieldset .input textarea, .formulario fieldset .input select {
  border: 1px solid #e1e1e1;
  border-radius: 0.5rem;
  padding: 0.5rem;
}
.formulario fieldset .input textarea {
  height: 15rem;
}
.formulario fieldset .input select {
  -webkit-appearance: none;
}
.formulario fieldset .options {
  display: flex;
}
.formulario fieldset .options .opcion:last-of-type {
  margin-left: 2.5rem;
}

.alerta {
  padding: 1rem 2rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: #a30202;
}
.alerta.exito {
  background-color: #71B100;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #71B100;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.propiedades .imagen-tabla {
  width: 10rem;
}

.imagen-small {
  margin-top: 1rem;
  width: 20rem;
}